*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
  }
  .head_listing{
    font-size: 30px;
    font-family: poppins;
    font-weight: bold;
    width: 80%;
    letter-spacing: normal;
    margin: 10px;
  }
  .imagecontainer{ 
      background-position: center;
      background-repeat: no-repeat;
      background-size: cover;
      position: relative;
          color: red;
      margin-top: 52px;
      /*height: 50%;*/
      /*width: ;*/
  }
  .imagecontainer img{
  object-fit: cover;
  height: 480px;
  width: 100%;
  }
  .click{
    position: absolute;
    bottom: 24px;
    right: 27px;
    color: #192f60;
    border-radius: 3px;
    padding-top: 13px;
    padding-left: 16px;
    padding-bottom: 12px;
    padding-right: 13px;
    border: 1px solid white;
    line-height: 30px;
    font-size: 16px;
    text-align: left;
    background-color: rgba(255,255,255,0.8);
    font-weight: bold;
      font-family: 'Poppins', sans-serif;
  }
  
