/*checkbox*/
ul li{
  list-style-type: none;
  margin: 15px;
  color: #636f88;
}

/*Sort_toggle*/
.nav{
  width: 100%;
  height: 30px;
  box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.16);
  z-index: 1;
  position: relative;
  color: yellow;
  border: 1px solid red;
  transition: 1s;
}
.glyphicon{
  color: black;
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
}

/*Normal*/
.sort{
  padding: 20px;
  width: 260px;
  height: 600px;
  box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.16);
  background-color: white;
}

.Head{
  /*width: 512px;*/
  height: 51px;
  font-family: Poppins;
  font-size: 36px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.53;
  letter-spacing: normal;
  text-align: left;
  color: #192f60;  
}
